@media screen and (min-width: 320px) {
  .padding {
    padding-top: 72px;
    padding-bottom: 50px;
    padding-left: 22px;
    padding-right: 22px;
  }
  .ul {
    gap: 30px;
  }
  .preH2 {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.375;
  }
  .h2 {
    font-weight: 400;
    font-size: 32px;
    line-height: 1.375;
  }
  .postH2 {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.375;
    margin-bottom: 63px;
  }
}
@media screen and (min-width: 768px) {
  .div {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
  }
  .padding {
    padding-top: 50px;
    padding-bottom: 40px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .ul {
    grid-template-columns: repeat(3, 1fr);
    gap: 18px;
  }
  .preH2 {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.65;
  }
  .h2 {
    font-weight: 400;
    font-size: 40px;
    line-height: 1.35;
  }
  .postH2 {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    margin-bottom: 24px;
  }
}

@media screen and (min-width: 1360px) {
  .div {
    width: 1360px;
  }
  .padding {
    padding-top: 80px;
    padding-bottom: 88px;
    padding-left: 28px;
    padding-right: 28px;
  }
  .preH2 {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.35;
  }
  .h2 {
    font-weight: 400;
    font-size: 40px;
    line-height: 1.35;
  }
  .postH2 {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    margin-bottom: 36px;
  }
}
.div {
  display: flex;
  background-color: var(--primary-text-color);
}
.preH2 {
  text-align: center;
  color: var(--secondary-text-color);
  margin-bottom: 16px;
}
.h2 {
  text-align: center;
  color: var(--secondary-text-color);
  margin-bottom: 24px;
}
.postH2 {
  text-align: center;
  color: var(--secondary-text-color);
}
.ul {
  display: grid;
}

.li:hover .img,
.li:focus .img {
  filter: brightness(0.6);
}
.li:hover .social,
.li:focus .social {
  visibility: visible;
}
.img {
  margin-bottom: 16px;
}
.picture {
  position: relative;
}
.social {
  visibility: hidden;
  overflow: hidden;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.social__ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}
.social__li {
  justify-content: center;
  align-items: center;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}
.social__a {
  fill: var(--primary-text-color);
}
.social__a:hover,
.social__a:focus {
  opacity: 0.8;
}

.personName {
  text-align: center;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.35;
  color: var(--secondary-text-color);
  margin-bottom: 8px;
}
.position {
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.375;
  color: var(--secondary-text-color);
}
